import React from 'react'
import LogoUrl from './imgnet.png'
import { NavLink, useNavigate } from 'react-router-dom'
import styled from 'styled-components'
import { Button } from 'antd'
import { useStores } from '../stores'
import { observer } from 'mobx-react'

const Header = styled.header`
  display: flex;
  align-items: center;
  padding: 10px 100px;
  background-color: #02101f;
  color: #fff;
`
const Logo = styled.img`
  height: 30px;
`
const StyledLink = styled(NavLink)`
  color: #fff;
  margin-left: 30px;
  &.active {
    border-bottom: 1px solid #fff;
  }
`
const Login = styled.div`
  margin-left: auto;
`
const StyledButton = styled(Button)`
  margin-left: 10px;
`

const Component = observer(() => {

  const navigate = useNavigate()
  const { UserStore, AuthStore } = useStores()
  // console.log( AuthStore )
  // console.log( UserStore )

  const handleLogout = () => {
    AuthStore.logout()
  }

  const handleLogin = () => {
    navigate('/login')
  }

  const handleRegister = () => {
    navigate('/register')
  }

  return (
    <Header>
      <Logo src={LogoUrl} alt='' />
      <nav>
        <StyledLink to="/" activeclassname="active" exact="true"></StyledLink>
        <StyledLink to="/history" activeclassname="active"></StyledLink>
        <StyledLink to="/about" activeclassname="active"></StyledLink>
      </nav>
      <Login>
        {
          UserStore.currentUser ? <>
            { UserStore.currentUser.attributes.username }<StyledButton type="primary" onClick={handleLogout}></StyledButton>
          </> : <>
            <StyledButton type="primary" onClick={handleLogin}></StyledButton>
            <StyledButton type="primary" onClick={handleRegister}></StyledButton>
          </>
        }
      </Login>
    </Header>
  )
})

export default Component