import React from 'react'
import styled from 'styled-components'

const Footer = styled.footer`
  padding: 50px 100px;
  text-align: center;
  font-size: 14px;
  color: #aaa;
`
const A = styled.a`
  cursor: pointer;
`

function Component() {
  return (
    <Footer>
      <p> |  | <A target="_blank" href="https://stnhu.me"></A></p>
    </Footer>
  )
}

export default Component