import { observable, action, makeObservable, configure } from 'mobx'
import { Uploader } from '../models'
import { message } from 'antd'

configure({
  enforceActions: 'never'
})

class HistoryStore {
  constructor() {
    makeObservable(this)
  }

  @observable list = []
  @observable isLoading = false
  @observable hasMore = true
  @observable page = 0
  limit = 6

  @action append(newList) {
    this.list = this.list.concat(newList)
    this.list.reverse()
  }

  @action find() {
    this.isLoading = true
    Uploader.find({page: this.page, limit: this.limit})
      .then(newList => {
        this.append(newList)
        this.page++
        if(newList.length < this.limit) {
          this.hasMore = false
        }
      })
      .catch(_ => {
        message.error('')
      })
      .finally(() => {
        this.isLoading = false
      })
  }

  @action destory(id) {
    Uploader.destory(id)
    console.log('destory ')
    setTimeout(() => {
      this.reset()
      this.find()
    }, 500)
  }

  @action reset() {
    this.list = []
    this.isLoading = false
    this.hasMore = true
    this.page = 0
  }
}

export default new HistoryStore()

